@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque&family=Lato&display=swap);

html{
  scroll-behavior: smooth;
  overflow-x: hidden;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, h2, h4{
  font-family: 'Darker Grotesque', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

canvas.webgl
{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px !important;
    height: 400px !important;
    text-align: center;
    background: rgba(99, 118, 131, 0);
    color: #fff;
    cursor: pointer;
}
canvas.webgl h2
{
    padding-top: 70px;
}
.webglCtnr{
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.webglCtnr p{
    color: rgba(128, 128, 128, 0.726);
}
.square
{
    border: red 1px solid;
    position: relative;
    width: 100% !important;
    min-height: calc(100vh - 64px) !important;
}
.titles{
    position: absolute;
    /* color: white; */
}
.hero{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 64px);
    position: relative;
}
.hero h1{
    padding-top: 70px;
    padding-bottom: 25px;
    font-size: 50px;
}

.illustration{
    max-width: 80%;
    position: relative;
}
.hero-body-center{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    
}
.profilePicCtnr{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    background-color: rgb(241, 241, 241);
    box-shadow: 27px 26px 20px -27px rgba(0,0,0,0.1);
    z-index: 1;
}
.profilePicCtnr div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 170px;
    height: 170px;
    border-radius: 50%;
    background-color: rgb(219, 219, 219);
}
.illustrationCtnr{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hero-body{
    font-family: 'Lato', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 200px;
    width: 650px;
}
.hero-foot{
    width: auto;
    position: absolute;
    left: calc(50% - 226px);
    bottom: 0;
}
.aboutDescription{
    text-align: center;
    
}
.profilePic{
    border-radius: 50%;
}

@media screen and (min-width: 769px){
    .illustration {
        max-width: 500px;
    }
}
.skills
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 64px);
    text-align: center;
    background: #585191;
    color: #fff;
}

.skills h2
{
    padding-top: 60px;
    padding-bottom: 30px;
}

.skillsCtnr
{
    display: flex;
    justify-content: space-between;
    width: 500px;
    height: auto;
    padding: 30px 0;
}

.techCtnr
{
    display: flex;
    justify-content: space-between;
    width: 350px;
    padding-top: 30px;

}
.appCtnr
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 250px;
    padding-top: 30px;

}
.restCtnr{
    display: flex;
}
.skillsDescription{
    width: 500px;
}
.icon 
{
    position: relative;
}
.icon .tooltiptext 
{
    opacity: 0;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.425);
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    transition: opacity .2s;
    will-change: opacity;
    /* Position the tooltip */
    z-index: 1;
    /* margin-left: -60px; */
  }
.icon:hover{
    cursor: pointer;

  }
.icon:hover .tooltiptext{
    opacity: 1;
    transition: opacity .2s;
}
#skillsDescription{
    width: 50%;
}
.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 64px);
    text-align: center;
}

.projects h2 {
    padding-top: 30px;
}

.projectsCtnr {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: calc(100vh - 132px);
    overflow: hidden;
    flex-wrap: wrap;
}

.project {
    width: 450px;
    height: 250px;
    margin: 20px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectDataCtnr {
    background: #585191;
    color: #fff;
    border-radius: 3px;
    width: 96%;
    height: 96%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectData {
    width: 90%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.badge {
    text-transform: uppercase;
    padding: 5px;
    position: absolute;
    top: 0;
    left: 20px;
}

.badge.sideProject {
    color: #fff;
    padding: 5px;
    position: absolute;
    top: 0;
    left: 20px;
    background: #81E979;
}

.badge.courseProject {
    color: #fff;
    padding: 5px;
    position: absolute;
    top: 0;
    left: 20px;
    background: #C6CA53;
}

.dataBtn {
    width: 100px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    background-color: #e6e6e6;
    border: none;
    color: #000000;
    text-decoration: none;
    font-size: 16px;
}

.dataBtn:hover {
    color: white;
    text-decoration: none;
}
.contact{
    display: flex;
    align-items: center;
    min-height: calc(35vh - 64px);
    text-align: center;
    background: #482c3d;
    color: #fff;
}
.contactData{
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

}
.contact h2{
    padding-top: 30px;
}
.contactIcons{
    width: 250px;
    height: auto;
    display: flex;
    justify-content: space-between;
}
.emailCtnr{
    display: flex;
    justify-content: center;
    width: 200px;
    height: auto;
    /* background: #fff; */
    border-radius: 2px;
    /* border: #000 solid 1px; */
}
.emailCtnr p{
    margin: 0;
    padding: 10px;
}
.emailCtnr p a{
    font-size: 20px;
    margin: 0;
    padding: 10px;
}
