.contact{
    display: flex;
    align-items: center;
    min-height: calc(35vh - 64px);
    text-align: center;
    background: #482c3d;
    color: #fff;
}
.contactData{
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

}
.contact h2{
    padding-top: 30px;
}
.contactIcons{
    width: 250px;
    height: auto;
    display: flex;
    justify-content: space-between;
}
.emailCtnr{
    display: flex;
    justify-content: center;
    width: 200px;
    height: auto;
    /* background: #fff; */
    border-radius: 2px;
    /* border: #000 solid 1px; */
}
.emailCtnr p{
    margin: 0;
    padding: 10px;
}
.emailCtnr p a{
    font-size: 20px;
    margin: 0;
    padding: 10px;
}